
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count:0,
conent:"ABCD24ABCD2-17ABCD3-16"
}
},
methods: {
radios: function() {
var _this = this;
_this.count = 0;

$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
if(_this.count=="0" && _this.count=="3"){
_this.conent="ABCD2"+_this.count+"ABCD2-1"+_this.count+"ABCD3-1"+_this.count+"";
}else if(_this.count=="4" && _this.count=="6")
{
_this.conent="ABCD2"+_this.count+"ABCD2-1"+_this.count+"ABCD3-1"+_this.count+"";
}else if(this.count=="7" && _this.count=="10")
{
_this.conent="ABCD2"+_this.count+"ABCD2-1"+_this.count+"ABCD3-1"+_this.count+"";
}
}
}
}



