
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count: 0,
conent: "9"
}
},
methods: {
radios: function() {
var _this = this;
$("input:checked").each(function() {

_this.count = _this.count + Number($(this).val());


})
if(_this.count >= "3" || _this.count <= "8") {
_this.conent = "3-8";
} else if(_this.count >= "9" || _this.count <= "12") {
_this.conent = "9-12";
} else if(_this.count >= "13" || _this.count <= "15") {
_this.conent = "13-15";
}
}
}
}



