
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count: 0,
conent: "0  1 .9 %  "
}
},
methods: {
radios: function() {
var _this = this;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());

if(_this.count == "0") {
_this.conent = "1 - 2.8% INR2-3** ";
} else if(_this.count == "1") {
_this.conent = "1  INR2-3** ";
} else if(_this.count == "2") {
_this.conent = "2  4.0(% INR2-3*** ";
} else if(_this.count == "3") {
_this.conent = "3  5.9(% INR2-3*** ";
} else if(_this.count >= "4") {
_this.conent = ">=4    >8(%  INR2-3*** ";
}
})
}
}
}



