
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count:0,
conent:"0-22.4%23.8%2.4%"
}
},
methods: {
radios: function() {
var _this = this;
$("input:checked").each(function() {

_this.count = _this.count + Number($(this).val());



})
if(_this.count=="0"||_this.count<="2"){
_this.conent="0-22.4%23.8%2.4%";
}else if(_this.count>="2"||_this.count<="5")
{
_this.conent="3-56.8%52.9%12.9%";
}else if(this.count>="6"||_this.count<="9")
{
_this.conent="6-913.2%73.3%36.2%";
}
}
}
}



